 path,\n        circle {\n            fill: #fff;\n        }\n    }\n\n    &.dplayer-fulled, &.dplayer-fulled-browser {\n        width: 100% !important;\n        height: 100% !important;\n        background: #000;\n        position: fixed;\n        z-index: 100000;\n        left: 0;\n        top: 0;\n        margin: 0;\n        padding: 0;\n        transform: translate(0, 0);\n    }\n\n    &.dplayer-no-danmaku {\n        .dplayer-setting-box {\n            .dplayer-setting-showdan,\n            .dplayer-setting-danmaku,\n            .dplayer-setting-danunlimit {\n                display: none;\n            }\n        }\n\n        .dplayer-controller .dplayer-icons .dplayer-comment {\n            display: none;\n        }\n\n        .dplayer-danmaku {\n            display: none;\n        }\n    }\n\n    &.dplayer-no-audio-switching {\n        .dplayer-setting-box {\n            .dplayer-setting-audio {\n                display: none;\n            }\n        }\n    }\n\n    &.dplayer-live {\n        .dplayer-time {\n            display: none;\n        }\n        .dplayer-bar-wrap {\n            display: none;\n        }\n        .dplayer-notice {\n            bottom: 56px;\n        }\n        .dplayer-setting-speed {\n            display: none;\n        }\n        .dplayer-setting-loop {\n            display: none;\n        }\n        .dplayer-controller .dplayer-icons .dplayer-setting-box {\n            height: auto;\n            overflow-y: hidden;\n        }\n        .dplayer-mobile-icon-backward, .dplayer-mobile-icon-forward {\n            display: none;\n        }\n\n        .dplayer-setting-box, .dplayer-comment-setting-box {\n            bottom: 58px;\n            max-height: calc(100% - 58px - 8px);\n        }\n        &.dplayer-mobile {\n            .dplayer-setting-box, .dplayer-comment-setting-box {\n                bottom: 52px;\n                max-height: calc(100% - 52px - 8px);\n            }\n        }\n    }\n\n    &.dplayer-narrow {\n        .dplayer-danmaku {\n            font-size: 18px;\n        }\n        .dplayer-icon {\n            margin: 0 -3px;\n        }\n    }\n\n    &.dplayer-playing {\n        .dplayer-danmaku .dplayer-danmaku-move {\n            animation-play-state: running;\n        }\n\n        .dplayer-controller-mask {\n            opacity: 0;\n        }\n        .dplayer-controller {\n            opacity: 0;\n        }\n\n        &:hover {\n            .dplayer-controller-mask {\n                opacity: 1;\n            }\n            .dplayer-controller {\n                opacity: 1;\n            }\n        }\n    }\n\n    &.dplayer-loading {\n        .dplayer-bezel .dplayer-loading-icon {\n            display: block;\n        }\n    }\n\n    &.dplayer-loading,\n    &.dplayer-paused {\n        .dplayer-danmaku,\n        .dplayer-danmaku-move {\n            animation-play-state: paused;\n        }\n    }\n\n    &.dplayer-show-controller {\n        .dplayer-controller-mask {\n            opacity: 1;\n        }\n        .dplayer-controller {\n            opacity: 1;\n        }\n    }\n\n    &.dplayer-fulled {\n        position: fixed !important;\n        z-index: 100000;\n        left: 0;\n        top: 0;\n        width: 100% !important;\n        height: 100% !important;\n\n        &.dplayer-hide-controller {\n            cursor: none;\n            .dplayer-controller-mask {\n                opacity: 0 !important;\n            }\n            .dplayer-controller {\n                opacity: 0 !important;\n            }\n        }\n    }\n\n    &.dplayer-fulled-browser {\n        &.dplayer-hide-controller {\n            cursor: none;\n            .dplayer-controller-mask {\n                opacity: 0 !important;\n            }\n            .dplayer-controller {\n                opacity: 0 !important;\n            }\n        }\n    }\n\n    &.dplayer-mobile {\n        .dplayer-controller {\n            height: 44px;\n            .dplayer-icons {\n                padding-top: 3px;\n                padding-bottom: 3px;\n                .dplayer-volume,\n                .dplayer-airplay-icon,\n                .dplayer-play-icon {\n                    display: none;\n                }\n                .dplayer-full .dplayer-full-in-icon {\n                    position: static;\n                    display: inline-block;\n                }\n                .dplayer-full .dplayer-pip-icon {\n                    position: static;\n                    display: inline-block;\n                }\n            }\n        }\n\n        .dplayer-controller-mask {\n            background: linear-gradient(rgba(0, 0, 0, 0.55) 78%, rgba(0, 0, 0, 0.8) 100%);\n            height: 100%;\n        }\n\n        &.dplayer-playing {\n            .dplayer-controller-mask {\n                opacity: 1;\n            }\n            .dplayer-controller {\n                opacity: 1;\n            }\n        }\n\n        &.dplayer-hide-controller {\n            .dplayer-controller-mask {\n                opacity: 0;\n            }\n            .dplayer-controller {\n                opacity: 0;\n                transform: translateY(100%);\n            }\n        }\n\n        .dplayer-bar-wrap {\n            bottom: 35px;\n            .dplayer-bar-time {\n                display: none;\n            }\n        }\n\n        .dplayer-setting-box, .dplayer-comment-setting-box {\n            bottom: 58px;\n            max-height: calc(100% - 58px - 8px);\n        }\n\n        &.dplayer-hide-controller {\n            .dplayer-mobile-icon-wrap {\n                visibility: hidden;\n                opacity: 0;\n            }\n        }\n\n        .dplayer-mobile-icon-wrap {\n            visibility: visible;\n            opacity: 0.7;\n        }\n    }\n}\n\n// To hide scroll bar, apply this class to <body>\n.dplayer-web-fullscreen-fix {\n    position: fixed;\n    top: 0;\n    left: 0;\n    margin: 0;\n    padding: 0;\n}\n",".dplayer-bezel {\n    position: absolute;\n    left: 0;\n    right: 0;\n    top: 0;\n    bottom: 0;\n    font-size: 22px;\n    color: #fff;\n    pointer-events: none;\n    .dplayer-bezel-icon {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        margin: -26px 0 0 -26px;\n        height: 52px;\n        width: 52px;\n        padding: 12px;\n        box-sizing: border-box;\n        background: rgba(0, 0, 0, .5);\n        border-radius: 50%;\n        opacity: 0;\n        pointer-events: none;\n        will-change: transform;\n        &.dplayer-bezel-transition {\n            animation: bezel-hide .5s linear;\n        }\n        @keyframes bezel-hide {\n            from {\n                opacity: 1;\n                transform: scale(1);\n            }\n            to {\n                opacity: 0;\n                transform: scale(2);\n            }\n        }\n    }\n    .dplayer-danloading {\n        position: absolute;\n        top: 50%;\n        margin-top: -7px;\n        width: 100%;\n        text-align: center;\n        font-size: 14px;\n        line-height: 14px;\n        animation: my-face 5s infinite ease-in-out;\n    }\n    .dplayer-loading-icon {\n        display: none;\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        margin: -18px 0 0 -18px;\n        height: 36px;\n        width: 36px;\n        pointer-events: none;\n        .dplayer-loading-hide {\n            display: none;\n        }\n        .dplayer-loading-dot {\n            animation: dplayer-loading-dot-fade .8s ease infinite;\n            opacity: 0;\n            transform-origin: 4px 4px;\n            will-change: transform;\n            @for $i from 7 through 1 {\n                &.dplayer-loading-dot-#{$i} {\n                    animation-delay: .1s * $i;\n                }\n            }\n        }\n        @keyframes dplayer-loading-dot-fade {\n            0% {\n                opacity: .7;\n                transform: scale(1.2, 1.2);\n            }\n            50% {\n                opacity: .25;\n                transform: scale(.9, .9);\n            }\n            to {\n                opacity: .25;\n                transform: scale(.85, .85);\n            }\n        }\n    }\n}\n","\n.dplayer-controller-mask {\n    position: absolute;\n    width: 100%;\n    height: 98px;\n    bottom: 0;\n    background: linear-gradient(rgba(0, 0, 0, 0.0) 15%, rgba(0, 0, 0, 0.7) 100%);\n    transition: all 0.3s ease;\n}\n\n.dplayer-controller {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    height: 50px;\n    padding: 0 20px;\n    user-select: none;\n    -webkit-user-select: none;\n    transition: all 0.3s ease;\n    &.dplayer-controller-comment {\n        .dplayer-icons {\n            display: none;\n        }\n        .dplayer-icons.dplayer-comment-box {\n            display: block;\n        }\n    }\n    .dplayer-bar-wrap {\n        position: absolute;\n        width: calc(100% - 40px);\n        height: 3px;\n        bottom: 41px;\n        padding: 10px 0;\n        cursor: pointer;\n        -webkit-tap-highlight-color: transparent;\n        // for preload thumbnails\n        // ref: https://stackoverflow.com/a/14390213/17124142\n        &:after {\n            // load thumbnails (if exists)\n            content: var(--thumbnail-url);\n            // hide the element\n            position: absolute;\n            width: 0;\n            height: 0;\n            overflow: hidden;\n            z-index: -1;\n        }\n        &:hover {\n            .dplayer-bar .dplayer-played .dplayer-thumb {\n                transform: scale(1);\n            }\n            .dplayer-highlight {\n                display: block;\n                width: 8px;\n                transform: translateX(-4px);\n                top: 4px;\n                height: 40%;\n            }\n        }\n        .dplayer-highlight {\n            z-index: 12;\n            position: absolute;\n            top: 5px;\n            width: 6px;\n            height: 20%;\n            border-radius: 6px;\n       